<template>
  <div class="g-addressList">
    <div class="m-address" v-show="addressList.length>0">
      <div class="s-box" v-for="(v,i) in addressList" :key="i" @click="order(v.id)">
        <div class="msg">
          <span class="name">{{v.contact}}</span>
          <span class="phone">{{v.phone}}</span>
        </div>
        <div class="details beyond">{{v.province}}{{v.city}}{{v.area}}{{v.address}}</div>
        <div class="btn after">
          <router-link :to="{name:'editaddress',query:{addressId:v.id}}" tag="div" class="edit">编辑</router-link>
          <div @click.stop="deleteAddress(v.id)" class="detel">删除</div>
        </div>
      </div>
    </div>
    <router-link :to="{name:'editaddress'}" tag="div" class="m-add">添加收货地址</router-link>
    <div class="m-ps" v-show="addressList.length==0">您还没有添加地址喔!</div>
  </div>
</template>

<script>
  export default {
    name: "addressList",
    data(){
      return {
        addressList:[],
        bit:{
          username:sessionStorage.getItem('userid'),
          page:1,
          pageSize:100
        }
      }
    },
    created(){
      this.List();//地址
    },
    methods:{
      deleteAddress(i){
        this.$createDialog({
          type: 'confirm',
          title: '是否删除地址',
          confirmBtn: {
            text: '删除',
            active: true,
            disabled: false,
            href: 'javascript:;'
          },
          cancelBtn: {
            text: '取消',
            active: false,
            disabled: false,
            href: 'javascript:;'
          },
          onConfirm: () => {
            console.log('确认',i)
            this.$post(this.$bee+'/api/user/deleteAddress',{id:i}).then((data)=>{
              if(data.code==200){
                this.$createToast({
                  txt: data.message,
                  time: 1000,
                  type: 'correct',
                  onTimeout: () => {
                    this.List();
                  }
                }).show()
              }else{
                this.$createToast({
                  txt: data.message,
                  type: 'txt'
                }).show();
              }
            })
          },
          onCancel: () => {
            console.log('取消',i)
          }
        }).show()
      },
      List(){
        this.$post(this.$bee+'/api/user/listAddress',this.bit).then((data)=>{
          if(data.code==200){
            this.addressList=data.data.adverts
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      order(v){//判断是否从订单页面进入
        if(this.$route.query.order){
          this.$store.commit("ADDRESSSAVE",v);
          this.$router.push({name:'orderConfirm'});
        }
      }
    }
  }
</script>

<style scoped lang="stylus">
.m-address
  .s-box
    padding 0 0 0 14px
    color: #5e5e5e;
    font-size 13px
    text-align left
    border-bottom 5px solid #ebebeb
    padding-top 10px
    .name
      widht 40px
    .phone
      margin-left 20px
    .details
      width 100%
      padding-top:15px
      padding-bottom 14px
      border-bottom 1px solid #dcdcdc
      padding-right 14px
      box-sizing border-box
    .btn
      padding 7px 19px
      box-sizing border-box
      &>div
        border: solid 1px #aeaeae;
        border-radius: 3px;
        width: 50px;
        height: 20px;
        line-height 20px
        color: #6c6c6c;
        text-align center
        float right
        margin-left 11px
.m-add
  padding 0 15px
  color: #6c6c6c;
  text-align left
  font-size 14px
  height 35px
  line-height 35px
  position relative
  background url(../assets/images/right-arrow.png) no-repeat center right
  background-size 7.5px 14px
  background-position calc(100% - 18px) center
  &:after
    content ''
    width calc(100% - 15px)
    height 1px
    background-color: #dcdcdc;
    position absolute
    bottom 0
    left 15px
.m-ps
  width 160px
  height 15px
  color: #ac4b4b;
  font-size: 15px;
  position absolute
  top calc(50% - 7.5px)
  left calc(50% - 80px)
</style>
